#home{display:-webkit-box;display:-ms-flexbox;display:flex;width:800px;margin:40px auto;border-radius:5px;background-color:#fff;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}#home .questlist_create,#home .questlist_rmall{background-color:#ee7017;border:none;color:#fff;width:100px;height:30px;border-radius:2px}#home .questlist-item:hover{background-color:#fef3ec}#home .home-empty_create{width:200px;height:40px;background-color:#ee7017;font-size:20px;border:none;color:#fff;text-align:center;cursor:pointer;border-radius:3px}#home .home-questlist{width:100%;padding:10px 0}#home .buttons button{font-size:16px;background-color:#ee7017;border:none;border-radius:2px;padding:3px 5px;color:#fff}#home .questlist{width:90%;margin:0 auto;border-collapse:collapse}#home .questlist .last-tr td{border:none;padding-top:20px}#home .questlist th{height:50px;font-size:18px;font-weight:bold}#home .questlist td{font-size:16px;height:40px;border-bottom:1px solid #ccc}#home .questlist td+td{text-align:center}


