#calendar{width:300px;border:1px solid #ccc;border-radius:3px;box-sizing:border-box;padding:10px;font-size:14px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}#calendar .calendar-selections{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around;width:100%;height:30px}#calendar .calendar-selections span{display:inline-block;height:24px;line-height:24px;cursor:pointer}#calendar .calendar-selections_year,#calendar .calendar-selections_month{width:90px;height:24px}#calendar .calendar-selections_year option,#calendar .calendar-selections_month option{text-align:center}#calendar table{width:100%;text-align:center;border-collapse:collapse}#calendar table,#calendar th,#calendar td{border:none}#calendar tr{height:26px}#calendar td{cursor:pointer;-webkit-transition:background-color .2s;transition:background-color .2s}#calendar td:hover{background-color:#fef3ec}#calendar .not-current-month{color:#aaa}


