#surface{position:fixed;left:0;top:0;width:100%;height:100%;background-color:rgba(0,0,0,0.3);font-size:16px}#surface .popup{position:absolute;overflow:hidden;top:50%;left:50%;margin-top:-200px;margin-left:-200px;width:400px;height:200px;border-radius:5px}#surface .popup-header{height:30px;background-color:#ee7419;color:#fff;line-height:30px;text-align:center}#surface .popup-content{height:130px;background-color:#fff;box-sizing:border-box;padding:10px}#surface .popup-footer{text-align:center;height:40px;background-color:#fff}#surface .popup-footer button{border:none;border-radius:2px;padding:0 10px;cursor:pointer;height:30px;background-color:#ee7419;color:#fff}#surface .popup-button_ok{margin-right:30px}#surface .popup-button_cancle{margin-left:30px}


